import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { TpgstoresComponent } from './tpgstores/tpgstores.component';
import { DealsComponent } from './deals/deals.component';
import { EntertainmentComponent } from './entertainment/entertainment.component';
import { ContactusComponent } from './contactus/contactus.component';
import { HomeServiceService } from './home-service.service';
import { LoginComponent } from './login/login.component';
import { TadepalliComponent } from './tadepalli/tadepalli.component';
import { SignupComponent } from './signup/signup.component';
import { SqrtPipe } from './sqrt.pipe';
import { NewComComponent } from './new-com/new-com.component';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { HeroDetailComponent } from './hero-detail/hero-detail.component';
import { SlideShowComponent } from './slide-show/slide-show.component';
import { SlideShowService } from './slide-show.service';



@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    TpgstoresComponent,
    DealsComponent,
    EntertainmentComponent,
    ContactusComponent,
    LoginComponent,
    TadepalliComponent,
    SignupComponent,
    SqrtPipe,
    NewComComponent,
    HeroDetailComponent,
    SlideShowComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    ReactiveFormsModule,
    FormsModule,
    CommonModule,
    RouterModule.forRoot([
        { path: 'tadepalli',
          component: TadepalliComponent,
          children: [
            {
              path: 'home',
              component: HomeComponent
            },
            {
              path: 'tpgstores',
              component: TpgstoresComponent
            },
            {
              path: 'deals',
              component: DealsComponent
            },
            {
              path: 'entertainment',
              component: EntertainmentComponent
            },
            {
              path: 'contactus',
              component: ContactusComponent
            },
          ]
        },
      {path: 'login', component: LoginComponent},
      {path: '', redirectTo : 'tadepalli/home', pathMatch: 'full'}
    ]),
  ],
  providers: [HomeServiceService,
              SlideShowService],
  bootstrap: [AppComponent],
  exports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class AppModule { }
